.App {
  height: 100%;
}

.App-header {
  padding-top: 10px;
  text-align: center;
  background-color: lightskyblue;
}

.App-title {
  font-size: 1.5em;
}

.App-body {
  font-size: large;
  margin: 20px;
  height: 100%;
}

#root {
  height: 100%;
}

.lookup {
  margin-top: 10px;
  margin-bottom: 10px;
}

h1 {
  padding: 10px;
}

td {
  border: 1px solid #cccccc;
  text-align: left;
  margin: 0;
  padding: 6px 13px;
}
th {
  border: 1px solid #cccccc;
  text-align: left;
  margin: 0;
  padding: 6px 13px;
  font-weight: bold;
}

.defrule {
  background: #efffff;
  padding-left: 2px;
  padding-right: 2px;
}

.own {
  background: plum;
  padding-left: 2px;
  padding-right: 2px;
}

.inherited {
  background: #996099;
  padding-left: 2px;
  padding-right: 2px;
}

.suppressed {
  background: darkgray;
  text-decoration: line-through;
  padding-left: 2px;
  padding-right: 2px;
}
.suppressedDesc {
  background: lightgray;
  text-decoration: line-through;
  padding-left: 2px;
  padding-right: 2px;
}
.desc {
  padding-left: 2px;
  padding-right: 2px;
}

.access-list {
  border: 1px solid #cccccc;
  margin: 6px;
  padding: 13px;
}

.allow-access {
  background: forestgreen;
  text-align: center;
  color: white;
  margin: 10px;
  padding: 15px;
}

.deny-access {
  background: firebrick;
  text-align: center;
  color: white;
  margin: 10px;
  padding: 15px;
}

.partial-access {
  background: darkorange;
  text-align: center;
  color: white;
  margin: 10px;
  padding: 15px;
}

.restricted-access {
  background-image: repeating-linear-gradient(90deg, forestgreen, #ccc 3px);
  text-align: center;
  color: black;
  margin: 10px;
  padding: 15px;
}

.no-margin {
  margin: unset;
  padding: unset;
}

.with-border {
  border: 1px solid #cccccc;
}

.with-separator {
  border-bottom: 1px solid #cccccc;
}
